import React from 'react';
import Helmet from 'react-helmet';
import { useTranslation } from 'react-i18next';
import urljoin from 'url-join';

import config from '../../gatsby-config';

interface IProps {
  language?: string;
  title?: string;
  url?: string;
  description?: string;
  image?: string;
  schema?: any[];
}

const SEO = ({ language, url, title, description, image, schema, ...props }: IProps) => {
  const { t } = useTranslation();

  const baseUrl = config.siteMetadata.url;
  const defaultTitle = config.siteMetadata.title;
  const metaTitle = title === defaultTitle ? title : `${title} | ${defaultTitle}`;
  const metaDescription = description ? description : t('meta.description');
  const currentUrl = url.trim() === '' ? baseUrl : `${baseUrl}${url}`;
  const imageUrl = urljoin(baseUrl, image);

  schema.push({
    '@context': 'http://schema.org',
    '@type': 'ArtGallery',
    address: {
      '@type': 'PostalAddress',
      addressLocality: 'Bremen, Deutschland',
      addressRegion: 'DE-HB',
      postalCode: 'D. 28203',
      streetAddress: 'Fedelhören 30',
    },
    name: defaultTitle,
    openingHours: [ 'Mo-Fr 14:00-18:00', 'Sa 10:00-13:00' ],
    telephone: '+49 (0) 421 / 32 37 47',
    faxNumber: '+49 (0) 421 / 32 81 07',
    url: baseUrl,
    image: `${baseUrl}/image`,
    email: 'contact@galerieroche.de',
    priceRange: '$$',
  });

  schema.push({
    '@context': 'http://schema.org',
    '@type': 'WebSite',
    alternateName: defaultTitle,
    name: defaultTitle,
    url: baseUrl,
  });

  return (
    <Helmet {...props}>
      <html lang={language} />
      <title>{metaTitle}</title>
      <meta name='title' content={metaTitle} />
      <meta name='application-name' content={metaTitle} />
      <meta name='description' content={metaDescription} />

      <meta property='og:type' content='website' />
      <meta property='og:url' content={currentUrl} />
      <meta property='og:title' content={metaTitle} />
      <meta property='og:description' content={metaDescription} />
      <meta property='og:image' content={imageUrl} />
      <meta property='og:site_name' content={metaTitle} />

      <meta name='twitter:card' content='summary' />
      <meta name='twitter:title' content={metaTitle} />
      <meta name='twitter:description' content={metaDescription} />
      <meta name='twitter:image' content={imageUrl} />
      <meta name='twitter:url' content={currentUrl} />
      <meta name='twitter:site' content={metaTitle} />
      <script type='application/ld+json'>{JSON.stringify(schema)}</script>
    </Helmet>
  );
};

SEO.defaultProps = {
  language: config.siteMetadata.defaultLanguage,
  url: config.siteMetadata.siteUrl,
  title: config.siteMetadata.title,
  image: config.siteMetadata.logo,
  schema: [],
};

export default SEO;
