import React from 'react';
import { withContext } from './PageContext';
import { Link as GatsbyLink } from 'gatsby';
import { Link, languages, withIntl } from '../i18n';

const routes = {
    home: '/',
    profile: '/profile',
    clients: '/clients',
    technologies: '/technologies',
    publications: '/publications',
};

const NavItems = {
    de: ['home', 'profile', 'clients', 'technologies', 'publications'],
    en: ['home', 'profile', 'clients', 'technologies'],
};

const PageHeader = ({ t, language = 'en', pageContext: { originalPath } }) => (
    <header>
        <nav>
            <ul>
                <li style={{ float: 'right' }}>
                    {Object.entries(languages).map(([code, label]) =>
                        code === language ? (
                            <span key={code}>{label}</span>
                        ) : (
                                <GatsbyLink
                                    activeClassName='active'
                                    key={code}
                                    to={`${code}${originalPath || ''}`}
                                >
                                    {label}
                                </GatsbyLink>
                            )
                    )}
                </li>
                {NavItems[language].map((item) => (
                    <li key={item}>
                        <Link to={routes[item]} activeClassName='active'>
                            {t(`navigation.${item}`)}
                        </Link>
                    </li>
                ))}
            </ul>
        </nav>
    </header>
);

export default withContext(withIntl()(PageHeader));
